import React, { useState } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';

export function Todo({
  title,
  completed,
  id,
  setTodos,
}) {
  const [onEditStatus, setEditStatus] = useState(false);
  const [todoText, setTodoText] = useState(title);

  const removeTodo = x => (
    setTodos(current => current.filter(elem => elem.id !== id))
  );

  const changeCheckbox = () => {
    setTodos(current => current.map((todo) => {
      if (todo.id === id) {
        return { ...todo, completed: !todo.completed };
      }

      return todo;
    }));
  };

  const onKeyPush = (key) => {
    if (key === 'Enter') {
      onBlur();
    } else if (key === 'Escape') {
      setTodoText(title);
      setEditStatus(false);
    }
  };

  const onBlur = () => {
    if (!todoText) {
      removeTodo(id);
      setEditStatus(false);
    } else {
      setTodoText(todoText.trim());
      setTodos(current => [...current].map((elem) => {
        if (elem.id === id) {
          return { ...elem, title: todoText };
        }

        return elem;
      }));
      setEditStatus(false);
    }
  };

  return (
    <li className={classnames({
      completed,
      editing: onEditStatus,
    })}
    >
      <div>
        <input
          type="checkbox"
          className="toggle"
          checked={completed}
          onChange={() => changeCheckbox()}
        />
        {!onEditStatus && (
          <label
            onDoubleClick={() => setEditStatus(true)}
          >
            {title}
          </label>
        )}
        <button
          type="button"
          className="destroy"
          onClick={() => removeTodo(id)}
        />
      </div>
      {onEditStatus && (
        <input
          autoFocus
          type="text"
          className="edit"
          value={todoText}
          onChange={event => setTodoText(event.target.value.trimLeft())}
          onKeyDown={event => onKeyPush(event.key)}
          onBlur={onBlur}
        />
      )}
    </li>
  );
}

Todo.propTypes = {
  completed: PropTypes.bool.isRequired,
  title: PropTypes.string.isRequired,
  id: PropTypes.number.isRequired,
  setTodos: PropTypes.func.isRequired,
};
